import { newBlueTheme } from './newBlue';
import { newYellowTheme } from './newYellow';
import { newCoralTheme } from './newCoral';
import { newPurpleTheme } from './newPurple';
import { fireTheme } from './fire';
import { lemonTheme } from './lemon';
import { skyTheme } from './sky';
